import VisionImg from "../images2/vision.jpg";

import AOS from "aos";
import "aos/dist/aos.css";
AOS.init();

export default function Vision() {
  return (
    <div className="vision">
      <div
        className="content_heading"
        data-aos="fade-in"
        data-aos-duration="1000"
      >
        <h3>Vision</h3>
      </div>
      <div className="vision_content">
        <div
          className="vision_text"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <p>
            The vision is to create an empowered workforce with the help of
            various schemes and training courses. MK FOUNDATION vision is
            to cater to skill development in India through a result-oriented
            framework that corresponds with the needs of the industry.
          </p>

          <p>
            We ensures high quality and cost-effective data conversion solutions
            through a highly competent and dedicated team of skilled staff.
          </p>
        </div>
        <div className="vision_img" data-aos="fade-in" data-aos-duration="1000">
          <img src={VisionImg} alt="VisionImg" />
        </div>
      </div>
    </div>
  );
}
