import LetsTalkImg from "../images2/lets-talk.jpg";

import AOS from "aos";
import "aos/dist/aos.css";
AOS.init();

export default function LetsTalk() {
  return (
    <div className="get_in_touch">
      <div className="get_in_touch_content">
        <div
          className="get_in_touch_content_heading"
          data-aos="fade-right"
          data-aos-duration="1000"
        >
          <h1>
            Empowerment Starts Here <span>Let's Talk!</span>
          </h1>
        </div>

        <div
          className="get_in_touch_content_text"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <p>
            Contact MK Foundation to explore how you can contribute to our
            mission of creating an empowered workforce through various skill
            development programs and initiatives. Whether you are an individual
            seeking training opportunities, a corporation interested in
            partnering for CSR initiatives, or a government body looking to
            collaborate on skill development projects, we welcome your
            involvement. Our dedicated team is here to assist you with any
            queries or proposals you may have. Join us in making a difference in
            the lives of marginalized communities and fostering economic growth
            across India. Together, let's harness the power of education and
            technology to create a brighter future for all.
          </p>
        </div>
      </div>

      <div
        className="get_in_touch_image"
        data-aos="fade-in"
        data-aos-duration="1000"
      >
        <img src={LetsTalkImg} alt="LetsTalkImg" />
      </div>
    </div>
  );
}
