import { Link } from "react-router-dom";

import FantasyFoundationImg from "../images/abc (2).jpg";

import AOS from "aos";
import "aos/dist/aos.css";
AOS.init();

export default function WhoWeAre() {
  return (
    <div className="whoweare_container">
      <div className="whoweare_content">
        <h1 data-aos="fade-in" data-aos-duration="1000">
          Who We Are?
        </h1>
        <p data-aos="fade-in" data-aos-duration="2000">
          MK Foundation is a non-profit organization situated in Solapur,
          Maharashtra. We started our journey with the dream of social
          upliftment and equality. Women empowerment is our prime concern. To
          give a proper life to the neglected people of our society, we fought,
          fight, and will fight. Fantasy Foundation work with 11 members, in the
          management. FANTASY Foundation is registered with the Registrar of
          Societies under the 1860 act of society registration and the type of
          NGO is Registered Societies (NonGovernment) and the Registration
          Number is MH/581/22, the date of registration is 02-09-2022.The city
          and state of registration are Solapur and MAHARASHTRA.
        </p>
        <div
          className="know_more_btn"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <Link to="/about">Know More</Link>
        </div>
      </div>

      <div
        className="whoweare_image"
        data-aos="fade-in"
        data-aos-duration="1000"
      >
        <img src={FantasyFoundationImg} alt="FantasyFoundationImg" />
      </div>
    </div>
  );
}
