import MissionImg from "../images/abc (3).jpg";

import AOS from "aos";
import "aos/dist/aos.css";
AOS.init();

export default function Mission() {
  return (
    <div className="mission">
      <div
        className="content_heading"
        data-aos="fade-in"
        data-aos-duration="1000"
      >
        <h3>Mission</h3>
      </div>
      <div className="mission_content">
        <div
          className="mission_img"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <img src={MissionImg} alt="MissionImg" />
        </div>
        <div
          className="mission_text"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <p>
            The mission is to create an empowered workforce with the help of
            various schemes and training courses. MK FOUNDATION vision is
            to cater to skill development in India through a result-oriented
            framework that corresponds with the needs of the industry.
          </p>

          <p>
            Enhancing the business growth of our customers to deliver
            high-quality solutions that create value and reliable competitive
            advantage for our clients around the world.
          </p>
        </div>
      </div>
    </div>
  );
}
