import React from "react";
import { Link } from "react-router-dom";

import AOS from "aos";
import "aos/dist/aos.css";
AOS.init();

export default function WhyChooseUs() {
  return (
    <div className="whychooseus_container">
      <div
        className="whychooseus_heading"
        data-aos="fade-in"
        data-aos-duration="1000"
      >
        <h1>Why Choose Us?</h1>
      </div>

      <div className="whychooseus_content">
        <div
          className="whychooseus_content_icon"
          data-aos="zoom-in"
          data-aos-duration="1000"
        >
          <i class="fa-solid fa-medal"></i>
        </div>
        <div
          className="whychooseus_content_text"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <h3>Proven Track Record of Impactful Collaborations</h3>
          <p>
            Mk Foundation has a demonstrated history of impactful
            collaborations with prominent organizations and government bodies.
             These collaborations have successfully
            addressed the skills gap and positively impacted thousands of lives
            through skill development programs.
          </p>
        </div>
      </div>

      <div className="whychooseus_content">
        <div
          className="whychooseus_content_icon"
          data-aos="zoom-in"
          data-aos-duration="1000"
        >
          <i class="fa-solid fa-gears"></i>
        </div>
        <div
          className="whychooseus_content_text"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <h3>Versatile Skill Development Initiatives</h3>
          <p>
            MK Foundation offers a diverse range of skill development
            initiatives, particularly in technology-related fields. From
            providing IT education aligned with industry needs to offering
            resources for entrepreneurship support, the foundation caters to
            various sectors and training needs, ensuring versatility and
            relevance in today's job market.
          </p>
        </div>
      </div>

      <div className="whychooseus_content">
        <div
          className="whychooseus_content_icon"
          data-aos="zoom-in"
          data-aos-duration="1000"
        >
          <i class="fa-solid fa-user-check"></i>
        </div>
        <div
          className="whychooseus_content_text"
          data-aos="fade-in"
          data-aos-duration="1000"
        >
          <h3>Commitment to Regional Development</h3>
          <p>
            With active engagement in Maharashtra, Odisha, and Karnataka,
            Fantasy Foundation is committed to contributing to the economic
            development of specific regions. By fostering local talent and
            addressing regional employment needs, the foundation plays a vital
            role in uplifting communities and driving regional growth.
          </p>
        </div>
      </div>

      <div
        className="read_more_btn"
        data-aos="fade-in"
        data-aos-duration="1000"
      >
        <Link to="/activities">Read More</Link>
      </div>
    </div>
  );
}
